"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import { 
  ShoppingBag, 
  Search, 
  Calendar, 
  Download, 
  Filter, 
  ChevronDown, 
  ChevronUp,
  ArrowUpDown,
  UserCircle,
  Package,
  Mail
} from "lucide-react";
import { Montserrat } from "next/font/google";

// Configuration de la police Montserrat
const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["400", "500", "600", "700", "800"],
  display: "swap",
});

// Données des ventes (simulées)
const salesData = [
  {
    id: "ORD-001",
    productImage: "/api/placeholder/80/80",
    productName: "T-shirt Vintage",
    purchaseDate: "2025-03-28T14:35:00",
    customerName: "Sophie Martin",
    customerEmail: "sophie.martin@example.com",
    quantity: 2,
    totalPrice: 4500,
    status: "Livré"
  },
  {
    id: "ORD-002",
    productImage: "/api/placeholder/80/80",
    productName: "Chaussures Sport",
    purchaseDate: "2025-03-27T09:22:00",
    customerName: "Thomas Dubois",
    customerEmail: "thomas.dubois@example.com",
    quantity: 1,
    totalPrice: 12000,
    status: "Expédié"
  },
  {
    id: "ORD-003",
    productImage: "/api/placeholder/80/80",
    productName: "Sac à dos Premium",
    purchaseDate: "2025-03-26T16:44:00",
    customerName: "Emma Leroy",
    customerEmail: "emma.leroy@example.com",
    quantity: 1,
    totalPrice: 8750,
    status: "En traitement"
  },
  {
    id: "ORD-004",
    productImage: "/api/placeholder/80/80",
    productName: "Pantalon Slim",
    purchaseDate: "2025-03-25T11:10:00",
    customerName: "Lucas Bernard",
    customerEmail: "lucas.bernard@example.com",
    quantity: 3,
    totalPrice: 9900,
    status: "Livré"
  },
  {
    id: "ORD-005",
    productImage: "/api/placeholder/80/80",
    productName: "Montre Élégante",
    purchaseDate: "2025-03-24T13:15:00",
    customerName: "Camille Petit",
    customerEmail: "camille.petit@example.com",
    quantity: 1,
    totalPrice: 15000,
    status: "Expédié"
  },
  {
    id: "ORD-006",
    productImage: "/api/placeholder/80/80",
    productName: "Casquette Simple",
    purchaseDate: "2025-03-23T10:05:00",
    customerName: "Hugo Roux",
    customerEmail: "hugo.roux@example.com",
    quantity: 4,
    totalPrice: 4000,
    status: "Livré"
  },
  {
    id: "ORD-007",
    productImage: "/api/placeholder/80/80",
    productName: "Chemise Élégante",
    purchaseDate: "2025-03-22T15:30:00",
    customerName: "Julie Moreau",
    customerEmail: "julie.moreau@example.com",
    quantity: 2,
    totalPrice: 7800,
    status: "En traitement"
  },
  {
    id: "ORD-008",
    productImage: "/api/placeholder/80/80",
    productName: "Bracelet Artisanal",
    purchaseDate: "2025-03-21T09:45:00",
    customerName: "Antoine Fournier",
    customerEmail: "antoine.fournier@example.com",
    quantity: 3,
    totalPrice: 3600,
    status: "Livré"
  }
];

const statusColors = {
  "Livré": "bg-green-100 text-green-800 border-green-300",
  "Expédié": "bg-blue-100 text-blue-800 border-blue-300",
  "En traitement": "bg-yellow-100 text-yellow-800 border-yellow-300",
  "Annulé": "bg-red-100 text-red-800 border-red-300"
};

const statusIcons = {
  "Livré": <span className="inline-block w-2 h-2 rounded-full bg-green-500 mr-2"></span>,
  "Expédié": <span className="inline-block w-2 h-2 rounded-full bg-blue-500 mr-2"></span>,
  "En traitement": <span className="inline-block w-2 h-2 rounded-full bg-yellow-500 mr-2"></span>,
  "Annulé": <span className="inline-block w-2 h-2 rounded-full bg-red-500 mr-2"></span>
};

export default function AdminSalesPage() {
  const [sales, setSales] = useState(salesData);
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState("Tous");
  const [sortConfig, setSortConfig] = useState({ key: "purchaseDate", direction: "desc" });
  const [isStatusDropdownOpen, setIsStatusDropdownOpen] = useState(false);
  const [selectedRows, setSelectedRows] = useState([]);
  const [selectAll, setSelectAll] = useState(false);

  // Fonction pour formater la date
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return new Intl.DateTimeFormat('fr-FR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  // Fonction pour trier les données
  const requestSort = (key) => {
    let direction = 'asc';
    if (sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setSortConfig({ key, direction });
  };

  // Appliquer le tri
  const sortedSales = [...sales].sort((a, b) => {
    if (a[sortConfig.key] < b[sortConfig.key]) {
      return sortConfig.direction === 'asc' ? -1 : 1;
    }
    if (a[sortConfig.key] > b[sortConfig.key]) {
      return sortConfig.direction === 'asc' ? 1 : -1;
    }
    return 0;
  });

  // Filtrer par terme de recherche et statut
  const filteredSales = sortedSales.filter(sale => {
    const matchesSearch = 
      sale.productName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      sale.customerName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      sale.customerEmail.toLowerCase().includes(searchTerm.toLowerCase()) ||
      sale.id.toLowerCase().includes(searchTerm.toLowerCase());
    
    const matchesStatus = statusFilter === "Tous" || sale.status === statusFilter;
    
    return matchesSearch && matchesStatus;
  });

  // Gérer la sélection des lignes
  const toggleRowSelection = (id) => {
    if (selectedRows.includes(id)) {
      setSelectedRows(selectedRows.filter(rowId => rowId !== id));
    } else {
      setSelectedRows([...selectedRows, id]);
    }
  };

  // Gérer la sélection de toutes les lignes
  const toggleSelectAll = () => {
    if (selectAll) {
      setSelectedRows([]);
    } else {
      setSelectedRows(filteredSales.map(sale => sale.id));
    }
    setSelectAll(!selectAll);
  };

  // Effet pour l'animation d'entrée
  useEffect(() => {
    const table = document.querySelector(".sales-table");
    const title = document.querySelector(".page-title");

    if (title) {
      title.animate(
        [
          { opacity: 0, transform: "translateY(-20px)" },
          { opacity: 1, transform: "translateY(0)" }
        ],
        {
          duration: 600,
          easing: "ease-out",
          fill: "forwards"
        }
      );
    }

    if (table) {
      table.animate(
        [
          { opacity: 0, transform: "translateY(30px)" },
          { opacity: 1, transform: "translateY(0)" }
        ],
        {
          duration: 800,
          easing: "ease-out",
          fill: "forwards",
          delay: 200
        }
      );
    }

    const rows = document.querySelectorAll(".sales-row");
    rows.forEach((row, index) => {
      row.animate(
        [
          { opacity: 0, transform: "translateX(-20px)" },
          { opacity: 1, transform: "translateX(0)" }
        ],
        {
          duration: 400,
          easing: "ease-out",
          fill: "forwards",
          delay: 300 + (index * 50)
        }
      );
    });
  }, []);

  // Calculer les statistiques des ventes
  const totalSales = filteredSales.reduce((sum, sale) => sum + sale.totalPrice, 0);
  const totalItems = filteredSales.reduce((sum, sale) => sum + sale.quantity, 0);
  const averageOrderValue = totalSales / filteredSales.length || 0;

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-200 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        <div className="flex items-center justify-between mb-8">
          <div className="flex items-center gap-3">
            <ShoppingBag className="h-10 w-10 text-indigo-600" />
            <h1 
              className={`page-title text-3xl font-extrabold text-gray-900 ${montserrat.className}`}
              style={{ fontFamily: "'Montserrat', sans-serif" }}
            >
              Mes Ventes
            </h1>
          </div>

          <div className="flex space-x-3">
            <button className="px-4 py-2 bg-white text-gray-700 rounded-lg shadow-md hover:bg-gray-50 transition-all flex items-center gap-2">
              <Filter className="h-4 w-4" />
              <span>Filtres avancés</span>
            </button>
            <button className="px-4 py-2 bg-indigo-600 text-white rounded-lg shadow-md hover:bg-indigo-700 transition-all flex items-center gap-2">
              <Download className="h-4 w-4" />
              <span>Exporter</span>
            </button>
          </div>
        </div>

        {/* Statistiques rapides */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="bg-white p-6 rounded-xl shadow-md border-l-4 border-indigo-500">
            <p className="text-sm font-medium text-gray-500 mb-1">Montant total des ventes</p>
            <p className="text-2xl font-bold text-gray-900">{totalSales.toLocaleString()} DZD</p>
          </div>
          <div className="bg-white p-6 rounded-xl shadow-md border-l-4 border-green-500">
            <p className="text-sm font-medium text-gray-500 mb-1">Quantité totale vendue</p>
            <p className="text-2xl font-bold text-gray-900">{totalItems} articles</p>
          </div>
          <div className="bg-white p-6 rounded-xl shadow-md border-l-4 border-amber-500">
            <p className="text-sm font-medium text-gray-500 mb-1">Valeur moyenne des commandes</p>
            <p className="text-2xl font-bold text-gray-900">{averageOrderValue.toLocaleString(undefined, { maximumFractionDigits: 0 })} DZD</p>
          </div>
        </div>

        {/* Barre de recherche et filtres */}
        <div className="flex flex-col md:flex-row gap-4 mb-6">
          <div className="relative flex-grow">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <Search className="h-5 w-5 text-gray-400" />
            </div>
            <input
              type="text"
              placeholder="Rechercher par produit, client ou commande..."
              className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg bg-white shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>

          <div className="relative">
            <button
              className="bg-white border border-gray-300 rounded-lg px-4 py-3 shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 flex items-center gap-2 w-full md:w-auto"
              onClick={() => setIsStatusDropdownOpen(!isStatusDropdownOpen)}
            >
              <span>Statut: {statusFilter}</span>
              {isStatusDropdownOpen ? <ChevronUp className="h-4 w-4" /> : <ChevronDown className="h-4 w-4" />}
            </button>
            
            {isStatusDropdownOpen && (
              <div className="absolute right-0 mt-2 w-56 bg-white border border-gray-200 rounded-lg shadow-lg z-10">
                <ul className="py-2">
                  {["Tous", "Livré", "Expédié", "En traitement", "Annulé"].map((status) => (
                    <li key={status}>
                      <button
                        className={`block px-4 py-2 text-sm w-full text-left hover:bg-gray-100 ${statusFilter === status ? 'bg-indigo-50 text-indigo-700' : ''}`}
                        onClick={() => {
                          setStatusFilter(status);
                          setIsStatusDropdownOpen(false);
                        }}
                      >
                        {status !== "Tous" && statusIcons[status]}
                        {status}
                      </button>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>

          <div className="relative">
            <button className="bg-white border border-gray-300 rounded-lg px-4 py-3 shadow-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 flex items-center gap-2 w-full md:w-auto">
              <Calendar className="h-4 w-4" />
              <span>Période</span>
              <ChevronDown className="h-4 w-4" />
            </button>
          </div>
        </div>

        {/* Tableau des ventes */}
        <div className="bg-white rounded-xl shadow-lg overflow-hidden sales-table mb-8">
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th scope="col" className="px-6 py-4">
                    <div className="flex items-center">
                      <input
                        type="checkbox"
                        className="h-4 w-4 text-indigo-600 border-gray-300 rounded focus:ring-indigo-500"
                        checked={selectAll}
                        onChange={toggleSelectAll}
                      />
                    </div>
                  </th>
                  <th scope="col" className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Produit
                  </th>
                  <th 
                    scope="col" 
                    className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                    onClick={() => requestSort('purchaseDate')}
                  >
                    <div className="flex items-center">
                      <span>Date d'achat</span>
                      <ArrowUpDown className="ml-1 h-4 w-4" />
                    </div>
                  </th>
                  <th scope="col" className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    <div className="flex items-center">
                      <span>Client</span>
                    </div>
                  </th>
                  <th 
                    scope="col" 
                    className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                    onClick={() => requestSort('quantity')}
                  >
                    <div className="flex items-center">
                      <span>Quantité</span>
                      <ArrowUpDown className="ml-1 h-4 w-4" />
                    </div>
                  </th>
                  <th 
                    scope="col" 
                    className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                    onClick={() => requestSort('totalPrice')}
                  >
                    <div className="flex items-center">
                      <span>Montant</span>
                      <ArrowUpDown className="ml-1 h-4 w-4" />
                    </div>
                  </th>
                  <th scope="col" className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Statut
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredSales.map((sale) => (
                  <tr 
                    key={sale.id} 
                    className="sales-row hover:bg-gray-50 transition-colors"
                  >
                    <td className="px-6 py-4 whitespace-nowrap">
                      <input
                        type="checkbox"
                        className="h-4 w-4 text-indigo-600 border-gray-300 rounded focus:ring-indigo-500"
                        checked={selectedRows.includes(sale.id)}
                        onChange={() => toggleRowSelection(sale.id)}
                      />
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-12 w-12 relative">
                          <Image
                            src={sale.productImage}
                            alt={sale.productName}
                            layout="fill"
                            objectFit="cover"
                            className="rounded-md"
                          />
                        </div>
                        <div className="ml-4">
                          <div className="text-sm font-medium text-gray-900">{sale.productName}</div>
                          <div className="text-xs text-gray-500">{sale.id}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <Calendar className="h-4 w-4 text-gray-400 mr-2" />
                        <div className="text-sm text-gray-900">{formatDate(sale.purchaseDate)}</div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex flex-col">
                        <div className="flex items-center text-sm font-medium text-gray-900">
                          <UserCircle className="h-4 w-4 text-gray-400 mr-2" />
                          {sale.customerName}
                        </div>
                        <div className="flex items-center text-xs text-gray-500 mt-1">
                          <Mail className="h-3 w-3 text-gray-400 mr-2" />
                          {sale.customerEmail}
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <Package className="h-4 w-4 text-gray-400 mr-2" />
                        <div className="text-sm text-gray-900">
                          {sale.quantity} {sale.quantity > 1 ? 'unités' : 'unité'}
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {sale.totalPrice.toLocaleString()} DZD
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`px-3 py-1 inline-flex text-xs font-semibold rounded-full border ${statusColors[sale.status]}`}>
                        {statusIcons[sale.status]}
                        {sale.status}
                      </span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>

        {/* Pagination */}
        <div className="flex items-center justify-between bg-white px-4 py-3 rounded-lg shadow-md">
          <div className="flex-1 flex justify-between sm:hidden">
            <a
              href="#"
              className="relative inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50"
            >
              Précédent
            </a>
            <a
              href="#"
              className="ml-3 relative inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50"
            >
              Suivant
            </a>
          </div>
          <div className="hidden sm:flex-1 sm:flex sm:items-center sm:justify-between">
            <div>
              <p className="text-sm text-gray-700">
                Affichage de <span className="font-medium">1</span> à <span className="font-medium">{filteredSales.length}</span> sur <span className="font-medium">{sales.length}</span> résultats
              </p>
            </div>
            <div>
              <nav className="relative z-0 inline-flex rounded-md shadow-sm -space-x-px" aria-label="Pagination">
                <a
                  href="#"
                  className="relative inline-flex items-center px-2 py-2 rounded-l-md border border-gray-300 bg-white text-sm font-medium text-gray-500 hover:bg-gray-50"
                >
                  <span className="sr-only">Précédent</span>
                  <ChevronDown className="h-5 w-5 rotate-90" />
                </a>
                <a
                  href="#"
                  aria-current="page"
                  className="z-10 bg-indigo-50 border-indigo-500 text-indigo-600 relative inline-flex items-center px-4 py-2 border text-sm font-medium"
                >
                  1
                </a>
                <a
                  href="#"
                  className="bg-white border-gray-300 text-gray-500 hover:bg-gray-50 relative inline-flex items-center px-4 py-2 border text-sm font-medium"
                >
                  2
                </a>
                <a
                  href="#"
                  className="bg-white border-gray-300 text-gray-500 hover:bg-gray-50 relative inline-flex items-center px-4 py-2 border text-sm font-medium"
                >
                  3
                </a>
                <span className="relative inline-flex items-center px-4 py-2 border border-gray-300 bg-white text-sm font-medium text-gray-700">
                  ...
                </span>
                <a
                  href="#"
                  className="relative inline-flex items-center px-2 py-2 rounded-r-md border border-gray-300 bg-white text-sm font-medium text-gray-500 hover:bg-gray-50"
                >
                  <span className="sr-only">Suivant</span>
                  <ChevronDown className="h-5 w-5 -rotate-90" />
                </a>
              </nav>
            </div>
          </div>
        </div>
      </div>

      {/* Styles CSS pour les animations */}
      <style jsx>{`
        @keyframes fadeAndSlideUp {
          from {
            opacity: 0;
            transform: translateY(20px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        
        .sales-row {
          opacity: 0;
          animation: fadeAndSlideUp 0.5s ease forwards;
        }
      `}</style>
    </div>
  );
}